$(document).ready(function() {

    // Btn-Fav
    $('.plywood__fav').click(function () {
        $(this).toggleClass('plywood__fav--active')
    })

    $('.plywood__comp').click(function () {
        $(this).toggleClass('plywood__comp--active')
    })

    // Tabs
    $(".plywood__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".plywood__tab").removeClass("plywood__tab--active")
        $(this).addClass("plywood__tab--active")

        $(this).parents(".plywood__tabs").find(".plywood__tabs-content").removeClass("plywood__tabs-content--active")
        $(this).parents(".plywood__tabs").find(`.plywood__tabs-content[data-content-path="${path}"]`).addClass("plywood__tabs-content--active")
    })

})